footer {
  height: 30vh;
  background-color: #b87a57;

  display: flex;
  justify-content: space-around;
  align-items: flex-start;

  padding: 3em 0;

  font-weight: 300;

  color: white;

  /* font-family: monserrat; */
}

/* logo */

.footer--logo--section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer--logo--image {
  height: auto;
  width: 15vw;
}

/* links */

.footer--contact--section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  height: 9em;
}

.footer--contact--title {
  font-size: 1.35em;
}

.footer--links {
  text-decoration: none;
  color: white;

  font-family: 'Urbanist', sans-serif;
  font-size: 1.1em;

  margin: 0.5em 0;
}

/* social media */
.footer--socialMedia--title {
  font-size: 1.35em;
}

.footer--socialMedia--section {
  display: flex;
  flex-direction: column;

  height: 10em;
}
