.custom {
  height: 80vh;
  width: 100%;
  min-height: 450px;
  position: relative;
}

.custom--image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.custom--text {
  position: absolute;
  right: 4vw;
  top: 15%;

  /* text-align: center; */

  /* z-index: 1; */

  margin-right: 0.5em;

  font-size: 2em;
}

.custom--button {
  position: absolute;
  right: 5vw;
  top: 25%;

  border-radius: 50%;
  background-color: black;
  color: white;

  padding: 1em;

  font-size: 1.2em;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
